import React from 'react';
import { render } from 'react-dom';
import Events from './patterns/events/events';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const events = document.querySelectorAll('.wrapper--events');
        events &&
            Array.from(events).forEach((event) => {
                finderConfig(event.dataset.config).then((config) => {
                    render(<Events config={config} element={event} />, event);
                });
            });
    }
});
